@import '../../assets/css/mixins';
@import '../../assets/css/theme';
.more-page {
  &-content {
    padding: 0 15px;
  }

  &-menu {
    width: 100%;
    background: $color-normal-card-bg;
    border-radius: 10px;
    padding: 15px 20px 15px 24px;
    margin: 16px 0;
    @include lightThemeOnly {
      box-shadow: 0px 0px 20px 0px rgba(16, 12, 29, 0.1);
    }
  }

  &-nav {
    &-item {
      display: flex;
      align-items: center;
      margin-bottom: 29px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &-icon {
      width: 22px;
    }

    &-text {
      flex: 1;
      margin-left: 15px;
      margin-right: 10px;
      font-size: 14px;
      font-weight: 600;
      color: $color-text-default;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    &-desc {
      font-size: 12px;
      font-weight: 400;
      color: rgba(255, 255, 255, 0.5);
    }
    &-switch {
      width: 44px;
      img {
        display: block;
        width: 100%;
        cursor: pointer;
      }
    }
  }

  &-language {
    background: #444454;
    border-radius: 10px 10px 0px 0px;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;

    &-item {
      height: 40px;
      line-height: 40px;
      font-size: 14px;
      font-weight: 600;
      color: #ffffff;
      text-align: center;

      &-active {
        background: #5a14e1;
        border-radius: 4px;
      }
    }
  }
}

@supports (bottom: constant(safe-area-inset-bottom)) {
  .more-page-language {
    padding-bottom: constant(safe-area-inset-bottom);
  }
}

@supports (bottom: env(safe-area-inset-bottom)) {
  .more-page-language {
    padding-bottom: env(safe-area-inset-bottom);
  }
}
