@import './var';

@mixin common-content-width {
  max-width: 1200px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  @include min-desktop-only {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
  }
  @include mobile-only {
    width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}

@mixin responseFont($fontSize: 14px, $xsFont: 12px, $line-height: 1.4) {
  line-height: $line-height;
  font-size: $fontSize;
  @include mobile-only {
    font-size: $xsFont;
  }
}

@mixin darkThemeOnly {
  html[data-theme='dark'] & {
    @content;
  }
}

@mixin lightThemeOnly {
  html[data-theme='light'] & {
    @content;
  }
}
@function useVar($varName) {
  @return var(--#{$varName});
}
